import { Component, ViewChild } from '@angular/core';
import { collectAndResolveStyles } from '@angular/core/src/animation/animation_style_util';
import { ModalDirective } from 'ngx-bootstrap';
import { AddUserService } from '../../userManager/components/userManger/adduser.service';
import { EditDetailPreview } from './previews/EditDetailPreview/editDetailPreview.component';
import { SchedulePreview } from './previews/schedulePreview.component';
import { TreatmentPreview } from './previews/treatmentPreview/treatmentPreview.component';
// import { Router } from '@angular/router';
// import { Subscription } from 'rxjs';
// import { MasterRepo } from '../../../common/repositories/masterRepo.service';
// import { AuthService } from '../../../common/services/permission/authService.service';
import { ReportService } from './report.service';
import { mergeMap } from 'rxjs/operators';
// import { Followup } from '../../../common/interfaces/master.interface';

@Component({
    selector: 'activity',
    templateUrl: './activityReport.component.html',
    providers: [ReportService, AddUserService]
})

export class ActivityReport {
    @ViewChild('schedulePreview') childSchedulePreview: SchedulePreview;
    @ViewChild('treatmentPreview') childTreatmentPreview: TreatmentPreview;
    @ViewChild('editDetailPreview') childEditDetail: EditDetailPreview;

    constructor(private reportService: ReportService, private userService: AddUserService) {
        this.userList = <any>[]
        this.formList = [];
        this.userService.getUserList()
            .pipe(mergeMap(d => d || []))
            .subscribe((data) => {
                this.userList.push(<any>data);
            },
                Error => console.log(Error),
                () => {
                    console.log(Error);
                });

        this.reportService.getForms()
            .subscribe((data) => {
                this.formList.push(<string>data);
            },
                Error => console.log(Error),
                () => {
                    console.log(Error);
                });
        this.reportObj = {
            Date1: new Date().toISOString().slice(0, 10),
            Date2: new Date().toISOString().slice(0, 10)
        };
        //console.log({reportObj_ctor : this.reportObj});
    }

    DialogMessage: string = '';
    @ViewChild('childModal') childModal: ModalDirective;
    @ViewChild('loginModal') loginModal: ModalDirective;

    reportObj: any = <any>{};
    userList: any[];
    formList: string[];


    reportList: any[] = [];

    onClickClear() { }

    loadReportEvent() {
        this.DialogMessage = "Activty Log Loading.... Please Wait...";
        this.childModal.show();
        //console.log("LOAD REPORT EVENT");
        try {
            let rparam = { rName: 'activityLog', rParam: this.reportObj }
            this.reportService.getReport(rparam, '/GetReport')
                .subscribe((data : any) => {
                    this.reportList = data;
                    //console.log("REPORT LIST LOAD REPORT", this.reportList, typeof(this.reportList[1].DATE));
                    this.DialogMessage = "Activity Log Loaded sucessfully...";
                    setTimeout(() => {
                        this.childModal.hide();
                    }, 1000)
                },

                    Error => {
                        try {
                            let t = Error.json();
                            //console.log(t);
                            this.DialogMessage = t.result + "Please check and try again";
                            //let p = t.json();
                            if (t.result == "Authorization Failed") {
                                //this.DialogMessage="erro in saving. " +Error._body;
                                setTimeout(() => {
                                    this.childModal.hide();
                                    this.loginModal.show();
                                }, 3000)
                            }
                            else {
                                this.DialogMessage = "Error in Loading Data:" + t.result;
                            }
                            setTimeout(() => {
                                this.childModal.hide();

                            }, 5000)

                        }
                        catch (e) {
                            this.DialogMessage = e
                            setTimeout(() => {
                                this.childModal.hide();

                            }, 3000)
                        }
                    })


        }
        catch (ex) {
            alert(ex);
            this.DialogMessage = ex;
            setTimeout(() => {
                this.childModal.hide();
            }, 3000)
        }
    }

    detailVisible(log) {
        if (log.activity.toLowerCase() == "delete") {
            switch (log.formName.toLowerCase()) {
                case "appointment":
                case "treatment master":
                    return true;
                default:
                    return false;
            }
        }
        else if(log.activity.toLowerCase() == "edit"){
            return true;
        }
            return false;
    }

    viewLogDetail(log) {
        console.log(log.formName.toLowerCase());
        if (log.activity.toLowerCase() == "delete") {
            switch (log.formName.toLowerCase()) {
                case "appointment":
                    this.childSchedulePreview.logId = log.logId;
                    this.childSchedulePreview.showDialog();
                    break;
                case "treatment master":
                    this.childTreatmentPreview.logId = log.logId;
                    this.childTreatmentPreview.showDialog();
                    break;
            }
        }
        else if(log.activity.toLowerCase() == "edit"){
            this.childEditDetail.logId = log.logId;
            this.childEditDetail.showDialog();
        }
    }
}