import { Component, ViewChild, Input } from '@angular/core';
import { ModalDirective } from 'ngx-bootstrap';
import { Router } from '@angular/router';
import { ReportService } from '../../report.service';
import { THIS_EXPR } from '@angular/compiler/src/output/output_ast';
import { MasterRepo } from '../../../../../common/repositories';
@Component({
    selector: 'editDetailPreview',
    styleUrls: ['../../../../schedule/schedule.scss'],
    templateUrl: './editDetailPreview.html'
})
export class EditDetailPreview {
    @ViewChild('editDetailPreview') childeditDetailPreviewModal: ModalDirective;
    @Input() logId: number = 0;
    log: any;

    constructor(private router: Router, private reportService: ReportService) {
        this.log = {};
    }

    ngOnInit() {
        this.log = {};
    }

    showDialog() {
        this.reportService.getLogDetail(this.logId).subscribe((response:any) => {
            this.log = response;
            console.log(this.log);
            setTimeout(() => {
                this.childeditDetailPreviewModal.show();
            }, 100);
        }
            , Error => {
                try {
                    let t = Error.json();
                    //console.log(t);
                    if (t.result == "Authorization Failed") {
                        this.router.navigate(['/login', this.router.url]);
                    }
                    else {
                        alert("Error in Loading Data:" + t.result);
                    }

                }
                catch (e) {
                    alert(e);
                }
            }
        );

    }

    labelClickEvent(id) {
        this.router.navigate(['/pages/schedule/scheInput', { mode: "edit", id: id, returnUrl: this.router.url }])
    }
}