import { Component, ViewChild } from '@angular/core';
import { ModalDirective } from 'ngx-bootstrap'
import { Router } from '@angular/router';
import { MasterRepo } from "../../../common/repositories/masterRepo.service";
import { AuthService } from "../../../common/services/permission/authService.service";
import { ReportService } from "./report.service";
import { Employee } from "../../../common/interfaces/master.interface";

@Component({
    selector: 'ER',
    templateUrl: './employeeReport.component.html',
    providers: [ReportService],
})
export class EmployeeReport {

    query: string = '';
    @ViewChild('childModal') childModal: ModalDirective;
    @ViewChild('loginModal') loginModal: ModalDirective;
    DialogMessage: string = "You are not authorized";
    reportList: any[] = [];
    reportObj: any = <any>{};
    EmployeeList: Employee[] = [];
    constructor(private masterService: MasterRepo, private _authService: AuthService, private router: Router, private reportService: ReportService) {
    }
    ngOnInit() {
        if (this.masterService._employeeList.length > 0) { this.EmployeeList = this.masterService._employeeList; }
        else {
            this.masterService.getEmployeeList().subscribe((data) => {
                this.EmployeeList.push(<Employee>data);
            },
                Error => console.log(Error),
                () => {
                    this.masterService._employeeList = this.EmployeeList;
                });
        }
    }
    onCancel() {
        this.reportObj = <any>{};
        this.reportList = [];
    }
    loadReportEvent() {
        this.DialogMessage = "Employee Report Loading.... Please Wait...";
        this.childModal.show();
        try {
            let rparam = { rName: 'employeeReport', rParam: this.reportObj }
            this.reportService.getReport(rparam, '/GetReport')
                .subscribe((data : any) => {
                    data.forEach(element => {
                        element.DATE = element.DATE.toString().substring(0, 10);
                    });
                    this.reportList = data;
                    this.DialogMessage = "Employee Report Loaded sucessfully...";
                    setTimeout(() => {
                        this.childModal.hide();
                    }, 1000)
                }
                    , Error => {
                        try {
                            let t = Error.json();
                            //console.log(t);
                            this.DialogMessage = t.result + "Please check and try again";
                            //let p = t.json();
                            if (t.result == "Authorization Failed") {
                                //this.DialogMessage="erro in saving. " +Error._body;
                                setTimeout(() => {
                                    this.childModal.hide();
                                    this.loginModal.show();
                                }, 3000)
                            }
                            else {
                                this.DialogMessage = "Error in Loading Data:" + t.result;
                            }
                            setTimeout(() => {
                                this.childModal.hide();

                            }, 5000)

                        }
                        catch (e) {
                            this.DialogMessage = e
                            setTimeout(() => {
                                this.childModal.hide();

                            }, 3000)
                        }
                    })
        }
        catch (ex) {
            alert(ex);
            this.DialogMessage = ex;
            setTimeout(() => {
                this.childModal.hide();
            }, 3000)
        }
    }

}