import { Component, ViewChild } from '@angular/core';
import { AuthService } from "./../../../../common/services/permission/authService.service";
import { ModalDirective } from 'ngx-bootstrap'
import { LocalDataSource } from '../../../../ng2-smart-table/ng2-smart-table';
//import 'style-loader!../smartTables.scss';
import { MasterRepo } from "../../../../common/repositories/masterRepo.service";
import { Branch, Employee } from '../../../../common/interfaces'
import { Router } from '@angular/router';
import { Subscription } from "rxjs";
import { deleteSetting } from 'src/app/data/disabeDates';

@Component({
  selector: 'employee-roster',
  templateUrl: './employeeRoster.component.html',
  providers: [],
})
export class EmployeeRosterComponent {
  query: string = '';
  DialogMessage: string = "You are not authorized";
  userBranches : Branch[] = [];
  selectedBranch : string;
  date: string;
  bsDate: string;

  branchSetting:any =  {
      enableCheckAll: true,
      text: 'Select Outlets',
      enableSearchFilter: true,
      labelKey:"branchName",
      primaryKey:"branchId",
      badgeShowLimit: 2
  };
  settings = {
    mode: 'external',    
    delete: null,
    edit: null,
    columns: {
      NAME: {
        title: 'Name',
        type: 'string'
      },
      ADDRESS: {
        title: 'Address',
        type: 'string'
      },
      TELNO: {
        title: 'Tel No.',
        type: 'string'
      },
      DESIGNATION: {
        title: 'Designation',
        type: 'string'
      },
      isAvailable: {
        title: 'Is Available',
        type: 'bool'
      },      
      remarks: {
        title: 'Reason',
        type: 'string'
      }
    }
  };
  private subcriptions: Array<Subscription> = [];
  source: LocalDataSource = new LocalDataSource();
  constructor(private service: MasterRepo, private _authService: AuthService, private router: Router) {
    this.userBranches = this._authService.getUserProfile().branches;
    this.selectedBranch = this._authService.getUserProfile().defaultBranch;
    let date = new Date();
    this.date = `${date.getFullYear().toString()}-${(date.getMonth() + 1).toString().padStart(2, '0')}-${date.getDate().toString().padStart(2,'0')}`;
    this.changeEntryDate(this.date, "AD");
  }

  get disableAdd(){
    return !this.menuRight.create;
  }

  get menuRight(){
    return this._authService.getRole.menuRights.find(x=>x.menuId === 'emp')
  }
  selectedRow:any;

  onViewClick(event): void {
   this.router.navigate(["/pages/masters/emp/detail-emp", { mode: "view", returnUrl: this.router.url, id: event.data.EMPLOYEEID }])
  }

  onEditClick(event): void {
     if (this._authService.getUserProfile() &&  this._authService.getUserProfile().Role=="admin") {
    this.router.navigate(["/pages/masters/emp/detail-emp", { mode: "edit", returnUrl: this.router.url, id: event.data.EMPLOYEEID }])
    } else {
      this.DialogMessage = "You are not authorized to edit employee."
      //this.childModal.show();
    }
  }

  changeEntryDate(value, format: string) {
    if(!value) return;
    if (format == "AD") {
      this.bsDate = this.service.toBSDate(value);
      this.loadData();
    }
    else if (format == "BS") {
      this.date = this.service.toADDate(value);
      this.loadData();
    }
  }

  loadData(){
    if(!this.date || !this.selectedBranch) return;
     let elist: Array<Employee> = [];
    this.service.getEmployeeRoster(this.selectedBranch, this.date).subscribe((data:any) => {
        let employees: Employee[] = data;
        elist.push(...employees)
      },
      Error => console.log(Error),
      () => {
        this.service._employeeList = elist;
        this.source.load(elist);
      });
  }

}
