import { MessageReport } from './components/messageReport.component';
import { Routes, RouterModule, CanActivate } from '@angular/router';
import { Reports } from './report.component';
import { CanActivateTeam } from '../../common/services/permission/guard.service';
import { CustomerReport } from "./components/customerReport.component";
import { EmployeeReport } from "./components/employeeReport.component";
import { RoomReport } from "./components/roomReport.component";
import { ServiceReport } from "./components/serviceReport.component";
import { SeriesReport } from './components/seriesReport.component';
import { FollowupReport } from './components/followupReport.component';
import { ActivityReport } from './/components/activityReport.component';
import { EmployeeRosterComponent } from './components/EmployeeRoster/employeeRoster.component';


const routes: Routes = [
    {
        path: '',
        component: Reports,
        children: [
            { path: 'cusReport', component: CustomerReport, canActivate: [CanActivateTeam] },
            { path: 'empReport', component: EmployeeReport, canActivate: [CanActivateTeam] },
            { path: 'roomReport', component: RoomReport, canActivate: [CanActivateTeam] },
            { path: 'serviceReport', component: ServiceReport, canActivate: [CanActivateTeam] },
            { path: 'seriesReport', component: SeriesReport, canActivate: [CanActivateTeam] },
            { path: 'patientRecords', component: SeriesReport, canActivate: [CanActivateTeam] },
            { path: 'followupRecords', component: FollowupReport, canActivate: [CanActivateTeam] },
            { path: 'messageReport', component: MessageReport, canActivate: [CanActivateTeam] },
            { path: 'activityReport', component: ActivityReport, canActivate: [CanActivateTeam] },
            { path: 'empRoster', component: EmployeeRosterComponent, canActivate: [CanActivateTeam] }

        ]

    }
];

export const routing = RouterModule.forChild(routes);