import { Component, ViewChild, Input } from '@angular/core';
import { ModalDirective } from 'ngx-bootstrap';
import { Router } from '@angular/router';
import { ReportService } from '../../report.service';
import { THIS_EXPR } from '@angular/compiler/src/output/output_ast';
import { MasterRepo } from '../../../../../common/repositories';
@Component({
    selector: 'treatmentPreview',
    styleUrls: ['../../../../schedule/schedule.scss'],
    templateUrl: './treatmentPreview.html'
})
export class TreatmentPreview {
    @ViewChild('treatmentPreview') childTreatmentPreviewModal: ModalDirective;
    @Input() logId: number = 0;
    treatment: any;
    contentUrl: string;

    constructor(private router: Router, private reportService: ReportService, private masterService:MasterRepo) {
        this.treatment = {};
        this.treatment.customer = {};
        this.treatment.healer = {};
        this.contentUrl = masterService.contentUrl;
    }

    ngOnInit() {
        this.treatment = {};
        this.treatment.patient = {};
        this.treatment.healer = {};
    }

    showDialog() {
        this.reportService.getLogDetail(this.logId).subscribe((response:any) => {
            this.treatment = response.model;
            document.getElementById("patientComplaint").innerHTML = this.treatment.patientComplaint;
            document.getElementById("treatmentNotes").innerHTML = this.treatment.treatmentNotes;

            setTimeout(() => {
                this.childTreatmentPreviewModal.show();
            }, 100);
        }
            , Error => {
                try {
                    let t = Error.json();
                    //console.log(t);
                    if (t.result == "Authorization Failed") {
                        this.router.navigate(['/login', this.router.url]);
                    }
                    else {
                        alert("Error in Loading Data:" + t.result);
                    }

                }
                catch (e) {
                    alert(e);
                }
            }
        );

    }

    labelClickEvent(id) {
        this.router.navigate(['/pages/schedule/scheInput', { mode: "edit", id: id, returnUrl: this.router.url }])
    }
}